.BlogInfo {
    width: 70%;
}
.card{
    margin-bottom: 30px;
}
.postImageCont img{
    width: 100%;
}
.blogHeader{
    text-align: center;
    padding: 20px 0;
}
.blogHeader p:nth-child(1){
    text-transform: uppercase;
}
.blogHeader h2{
    margin-top: 10px;
    margin-bottom: 10px;
}
.blogHeader p:nth-child(3){
    font-size: 14px;
    color: #666;
}
.blogHeader p:nth-child(4){
    font-size: 16px;
    color: #666;
    margin: 20px 0 20px 0;
}
.navLink{
    color: #666;
    width: 150px;
    padding: 7px 0;
    text-align: center;
    margin: auto;
    display: block;
    border: 1px solid #666;
    border-radius: 40px;
}
.blogContent p{
    padding: 20px;
}