@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Oswald', sans-serif;
}
body{
  background-color: #e9eaed;
}
ul{
  padding: 0;
  list-style: none;
}
a{
  text-decoration: none;
}
.App{
  width: 80%;
  margin: auto;
}
.card{
    background-color: #fff;
    width: 100%;
}
.blogPost {
    width: 70%;
}
.card{
    margin-bottom: 30px;
}
.postImageCont img{
    width: 100%;
}
.blogHeader{
    text-align: center;
    padding: 20px 0;
}
.blogHeader p:nth-child(1){
    text-transform: uppercase;
}
.blogHeader h2{
    margin-top: 10px;
    margin-bottom: 10px;
}
.blogHeader p:nth-child(3){
    font-size: 14px;
    color: #666;
}
.blogHeader p:nth-child(4){
    font-size: 16px;
    color: #666;
    margin: 20px 0 20px 0;
}
.navLink{
    color: #666;
    width: 150px;
    padding: 7px 0;
    text-align: center;
    margin: auto;
    display: block;
    border: 1px solid #666;
    border-radius: 40px;
}
.SideBar{
    width: 25%;
}
.asideCard{
    margin-bottom: 10px;
    padding: 10px;
}
.asideCard p:nth-child(1){
    font-size: 16px;
    color: #000;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    padding-bottom: 10px;
}
.asideCard img{
    width: 100%;
    display: block;
}
.asideCard p:nth-child(3){
    font-size: 14px;
    padding: 10px 0;
    color: #666;
}
.socialNet{
    display: flex;
    justify-content: center;
}
.socialNet li a{
    padding: 0 10px;
    font-size: 19px;
    color: #666;
}
.recent{
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: dotted 1px #D9D9D9;
}
.recent a {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.7;
    letter-spacing: 0.3px;
    color: #565673;
}
.recent p {
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 0.8px;
    color: #BCBCC2;
    padding-top: 5px;
}
.recent p i{
    font-family: fontawesome;
    padding-right: 4px;
}
.container{
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
}


.BlogInfo {
    width: 70%;
}
