.blogContent p{
    padding: 20px;
}

nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
}
.headerMenu li{
    margin-right: 25px;
    display: inline-block;
}
.headerMenu a{
    color: #565673;
    letter-spacing: 1.2px;
    font-size: 13px;
}
.socialMedia ul li{
    display: inline-block;
    margin-right: 20px;
}
.socialMedia ul li:last-child,.navMenu li:last-child{
    margin-right: 0;
}
.socialMedia ul li a{
    color: #565673;
    font-size: 16px;
}
.hero .card{
    padding: 50px 0;
}
.logo{
    display: block;
    margin: auto;
}
.navigation{
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navMenu li{
    display: inline-block;
    margin-right: 10px;
}
.navMenu a{
    color: #565673;
    letter-spacing: 1.2px;
    font-size: 20px;
    padding: 10px;
    display: block;
}
.search input{
    background: transparent;
    border: none;
    border-bottom: 1px solid #565673;
    outline: none;
    width: 0;
    padding-left: 5px;
    opacity: 0;
    display: inline-block;
    transition: width 0.5s ease-in;
}
.search .fa-search {
    padding: 0 10px;
    color: #565673;
    font-size: 14px;
    display: inline-block;
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
}
.search input.active{
    width: 200px;
    opacity: 1;
}

